<template>
  <div class="register" style="background-color: #fff;">
    <div class="register-bar">
      <van-nav-bar title="提交状态" left-arrow @click-left="linkHome"/>
    </div>
    <div class="submission">
      <div class="submission-img">
        <img src="../../../assets/examine/submission.png"/>
      </div>
      <div class="tips">
        <div>开户申请已提交</div>
        <div>预计3个工作日内反馈审核结果</div>
        <div>请耐心等候！</div>
      </div>
      <div class="steps">
        <van-steps direction="vertical" :active="0" active-color="#1988FF">
          <van-step>
            <img slot="active-icon" class="step-active" src="../../../assets/examine/stepsOk.png"/>
            <div>提交开户申请</div>
          </van-step>
          <van-step class="lastClass2">
            <img slot="inactive-icon" class="step-active step-inactive" src="../../../assets/examine/steps2.png" />
            <div>平台审核</div>
          </van-step>
          <van-step class="lastClass">
            <img slot="inactive-icon" class="step-active step-inactive" src="../../../assets/examine/steps3.png" />
            <div>公布审核结果</div>
          </van-step>
        </van-steps>
      </div>
      <div class="again" @click="linkHome">返回首页</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'submission',
  created(){
    let that = this
  },
  methods: {
    // 返回上级
    linkTBA(){
      this.$router.push({
        name:'toBeAudited'
      })
    },
    // 返回首页
    linkHome(){
      this.$router.push({
        name:'home',
        params:{
          status:3
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/register";
</style>



